import { Inline } from '@ali/4ever-cangjie';
import type { ValueToTextRule } from '@ali/4ever-cangjie';
import Link from '../models';

const valueToText: ValueToTextRule = {
  name: 'link',

  match(node) {
    return Link.isLink(node);
  },

  convert(node) {
    return node.text || (node as Inline).data.href;
  },
};

export default valueToText;
